@import url(https://fonts.googleapis.com/css2?family=Josefin+Slab:wght@400;500;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  min-height: 100vh;
  font-family: 'Josefin Slab', serif;
 
}

.navbar {
  text-transform: uppercase;
  background-color: blue;
}

.navbar.brand  {
  color: #000!important;
  text-transform: capitalize;
  font-weight: bold;
}

.nav-link {
  font-weight: 600;
  color: #fff!important;
}

.jumbotron  {
  margin-bottom: 0px;
}

#home {
  width: 100%;
  height: 80vh;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}


span {
  color: aqua;
}

.content-home h1 {
  font-size: 45px;
  font-weight: bold;
}

img {
  width: 50%;
}

#about {
  width: 100%;
  height: 80vh;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.content-about h1 {
  font-size: 45px;
  font-weight: bold;
}

 hr {
  width: 20%;
  height: 3%;
  margin: auto;
  margin-top: 4px;
  color: #4894b3!important;
}

.card {
  padding-top: 20px;
}

.footer {
  background-color: #000;
  color: #fff;
  height: 90px;
}
